.Services{
    align-items: center;
    justify-content: center;
    background-color: brown;
    max-width: 100vw;  
    padding-top: 2vh;
}
.row{
    display: flex;
    position: relative;
    /* align-items: center; */
    justify-content: center;
    background-color: brown;
  
}
.render{
    margin: 0vw 2vw 0vw 2vw;   
    max-width: 20vw;
    margin-top: 2vh;
    justify-content: center;
    align-content: center;
  
}
.profile-pic{
   /* width: 20vw;*/
    height: 20vh;
    justify-content: center;
    align-content: center;
}
h5{
    font-variant: small-caps;
    color: white;
    font-weight: 700;
    align-content: center;
    justify-content: center;
    text-align: center;

}
ul{
    font-size: 14px;
    color: white;
    
}
.title-page{
    align-content: center;
    text-align: center;
   
    
}
h1{
    font-variant: small-caps;
    color: white;
    font-weight: 500;
    align-content: center;
    justify-content: center;
    text-align: center;
}
.new{
    margin-top: 5vh;
}
.progress{
   margin-bottom: 3vh;
   height: 4vh;
   font-size: 15px;
   text-align: center;
}
hr.horizontal{
    border: 0;
    height: 1px;
    background-image: linear-gradient(to right, rgba(0, 0, 0, 0), rgb(240, 232, 232), rgba(0, 0, 0, 0));
}


/* For mobile screens*/
@media screen and (max-width:600px){
    .Services{
        display: block;
        /* justify-content: left; */
    }
    h5{
        font-size: 17px;
       
    }
    h1{
        font-size: 20px;
    }
        
    
    /* .title{
        float: right;
    } */
    .profile-pic{
        width: 40vw;
        height: 15vh;
        margin-left:25vw;
        /* justify-content: center;
        align-items: center;
        align-content: center; */
        /* float: left; */
        /* margin-left: 1vw ;       */
    }
  
    .render{
        /* display: flex;  */
        max-width: 100vw; 
        margin-top: 2vh; 
        justify-content: center;
        align-content: center;
        align-items: center;
         }

}
/*For Tablets and medium screens*/
